import React from "react"
import "./TodoItem.css"
import Divider from '@material-ui/core/Divider';
import DeleteOutlineTwoToneIcon from '@material-ui/icons/DeleteOutlineTwoTone';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import { makeStyles } from '@material-ui/core/styles';
import FormControlLabel from '@material-ui/core/FormControlLabel';



function TodoItem({item,setTodos,todos,setText,text}){
    function handleCheck(e){
        todos.map((p)=>{ if(p==item){
            p.alive=!p.alive} 
        })
        
        setTodos([...todos,])
        
        }
    function handleDeleteTodo(e){
        console.log("in del func()",todos)

        const upd=[]
        todos.map((it)=>{
            if (it!=item){
                upd.push(it)
            }
        })
        console.log(todos)
        setTodos([...upd])

    }



   
    


  
return (
    <>
    
    <div className="row">
    <div className="col-6">
    <div id="TodoItem" >
        
        {item.alive==false?<h4 className="non_strike" >{item.text}  </h4>:<h5  className="strike" >{item.text}  </h5>}


       
    </div>


    </div>
    <div className="col-3"><button onClick={handleDeleteTodo} className="del"><DeleteOutlineTwoToneIcon></DeleteOutlineTwoToneIcon></button>
    &nbsp;
   
    
    </div>
    <div className="col-3">
    <input   style={{color: "red",height:"17px"}} id="check" type="checkbox" name="" onChange={handleCheck} />

        
        
    </div>
    </div>
    <Divider />

    </>
    )
}
export default TodoItem