import React from 'react'
import "./Form.css"
import TextField from '@material-ui/core/TextField';
import AddIcon from '@material-ui/icons/Add';
import Alert from '@material-ui/lab/Alert';

  
//Functional component 
const Form = ({text,setText,todos,setTodos}) => { 
  //render single Form component 
  
  function handleTyping(e){
      e.preventDefault()
      setText(e.target.value)
  }
  function handleAddTodo(e){
      e.preventDefault()
      if(text!==" " && text!==""){
        setTodos([...todos,{"text":text,"alive":false}])
        setText(" ")

      }
      else{
          console.log("Cant add empty todo")
       
      }

      

  }
  return( 
      <div className="Form">
          
          <div className="row">
          <div className="col-sm-5"></div>
            <div className="col-sm-7">

            <form  id="form"><span style={{width:"2%"}}></span>
            <TextField
            value={text}
            onChange={handleTyping}
          id="standard-full-width"
          
          style={{ margin: 8 }}
          //placeholder="Add todo here"
          label="Add todo here"
        
          
          margin="normal"
          InputLabelProps={{
            shrink: true,
          }}
        />
              {/* <TextField  value={text} id="standard-basic" label="write todo here" onChange={handleTyping} />&nbsp; */}
              <button className="btn" onClick={handleAddTodo}><AddIcon></AddIcon></button>
          </form>
            </div>
            
          </div>
      </div>
  ) 
} 
  
export default Form