import React from "react"
import TodoItem from "./TodoItem"
function TodoComp({todos,setTodos,text,setText}){
    return (
        <>
        <br/><br/>

        
        <div className="row">
        <div className="col-sm-3"></div>
            <div className="col-sm-6">

            <div id="TodoComp">
            {todos.map((item,index)=> <TodoItem  key={index}  setText={setText} item={item} setTodos={setTodos} todos={todos}/> )}
            
            
        </div>



            </div>
            <div className="col-sm-3"></div>
        </div>

        </>
    )
}
export default TodoComp